<template>
  <div class="historical-rank">
    <template v-if="historicalData">
      <RankItem v-for="(item, index) in historicalData" :item="item" :key="index+`d`"
        @RankItemClick="RankItemClick($event)"></RankItem>
      <div class="end">——the end——</div>
    </template>
    <template v-else>
      <van-loading type="spinner" color="#6b6b6b" />
    </template>

  </div>
</template>

<script>
  import RankItem from "@/components/RankItem.vue";
  export default {
    name: 'historicalRank.vue',
    data() {
      return {
        historicalData: null
      }
    },
    beforeRouteEnter(to, from, next) {
      next((vm) => {
        vm.historicalData = vm.$store.state.DayRank
        
      })
    },
    methods: {
      RankItemClick($event) {
        this.$router.push('/playingdetailpage');
        this.$store.commit('setPlayingPageDate', $event)
        this.$store.commit('setShowBottomBar', false)
      },
    },
    components: {
      RankItem
    }

  }
</script>

<style lang="less" scoped>
  .historical-rank {
    .van-loading {
      text-align: center;
    }
    .end{
      font-weight: bold;
      color:#6b6b6b;
      text-align: center;
      padding:10px 0;
    }
  }
</style>